@import url(https://fonts.googleapis.com/css?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.recipies_recepie__2eVyP{
   border-radius: 20px;
   box-shadow: 0px 5px 20px rgb(71,71,71);
   margin: 20px;
   display: flex;
   flex-direction: column;
   justify-content: space-around;
   background: white;
   align-items: center;
   width: 30%;
}


@media(max-width:700px){
   .recipies_recepie__2eVyP{
      width: 80%;
      text-align: center;
   }


}
.App {
  background-image: -webkit-linear-gradient(45deg, #ff9a9e 0%, #fad0c4 99%, #fad0c4 100%);
  background-image: linear-gradient(45deg, #ff9a9e 0%, #fad0c4 99%, #fad0c4 100%);
  margin: 0;
  padding: 0;
}

.heading{
  text-align: center;
  width: 99vw;
  margin: 0;
  padding: 0;
  display: grid;
}

.heading h1{
  font-family: 'Great Vibes' , cursive;
  font-size: 5rem;
  font-weight: lighter;
  color: #e6891d;
  letter-spacing: 3px;
  word-spacing: 6px;
}

.search-form{
  min-height: 2vh;
  display: flex;
  justify-content: center;
  align-content: center;
  align-self: center;
}

.serach-bar{
  width: 50%;
  padding: 10px;
  border: none;
  margin: 2%;
  font-size: 1.5rem;
  color: #888;
}

input[type='submit']{
  border: none;
  height: 50px;
  width: 10%;
  margin-top: 2%;
  border-radius: 10px;
  background: #a18cd1;
  cursor: pointer;
  box-shadow: 0 0  3px 3px rgba(255,255,255,0.7) ;
  -webkit-transition: 1s;
  transition: 1s;
}

input[type='submit']:hover{
  box-shadow: none;
  -webkit-transition: 1s;
  transition: 1s;
}

.recipies{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}


@media (max-width: 700px){
  .search-form{
    min-height: 2vh;
    display: flex;
    justify-content: center;
    align-content: center;
    align-self: center;
    flex-direction: column;
  }
  
  .serach-bar{
    width: 90vw;
    padding: 10px;
    border: none;
    margin: 2%;
    font-size: 1.5rem;
    color: #888;
  }
  input[type='submit']{
    border: none;
    height: 50px;
    width: 80%;
    margin-top: 2%;
    border-radius: 10px;
    background: #a18cd1;
    cursor: pointer;
    box-shadow: 0 0  3px 3px rgba(255,255,255,0.7) ;
    -webkit-transition: 1s;
    transition: 1s;
    justify-content: center;
    align-content: center;
    align-self: center;
  }

}
